.hubspot-logo-color-2-icon {
  position: absolute;
  height: 60%;
  width: 85.22%;
  top: 20%;
  right: 7.28%;
  bottom: 20%;
  left: 7.5%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.freshbooks-logo-1-1-icon {
  position: absolute;
  top: calc(50% - 24px);
  left: calc(50% - 82px);
  width: 163.49px;
  height: 48px;
  object-fit: cover;
  display: none;
}
.frame-child,
.frame-inner,
.frame-item,
.rectangle-div {
  position: absolute;
  top: 0;
  background-color: var(--product-suite-message);
  opacity: 0.24;
}
.frame-child {
  width: 100%;
  right: 0;
  left: 0;
  height: 24px;
}
.frame-inner,
.frame-item,
.rectangle-div {
  width: 16px;
  height: 80px;
}
.frame-item {
  left: 0;
}
.frame-inner,
.rectangle-div {
  right: 0;
}
.rectangle-div {
  width: 100%;
  top: 56px;
  left: 0;
  height: 24px;
}
.guideline,
.rectangle-parent {
  position: absolute;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
}
.rectangle-parent {
  height: 80px;
  overflow: hidden;
  display: none;
  opacity: 0.4;
}
.guideline {
  height: 100%;
  bottom: 0;
}
.hubspot {
  position: relative;
  background-color: var(--color-white);
  width: 160px;
  height: 80px;
}
