@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-roboto: Roboto;

  /* font sizes */
  --font-size-13xl: 32px;
  --font-size-11xl: 30px;
  --font-size-lg: 18px;
  --font-size-6xl: 25px;
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-51xl: 70px;
  --font-size-mini: 15px;
  --font-size-45xl: 64px;
  --font-size-5xl: 24px;
  --font-size-sm: 14px;

  /* Colors */
  --color-white: #fff;
  --color-royalblue-100: #3474e8;
  --color-royalblue-200: #1565d8;
  --color-darkslateblue: #13357f;
  --color-black: #000;
  --product-suite-message: #37b8af;
  --color-darkgray: #adadad;
  --color-whitesmoke: #f9f9f9;
  --color-lightskyblue: #a7d7ff;
  --color-lightsteelblue: #c8c8ff;
  --color-blue-100: #0008ff;
  --color-blue-200: #0000ff;
  --color-slategray: #5a6a86;
  --color-lightslategray: #8692a6;
  --color-aliceblue: #e3f3ff;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-33xl-2: 52.2px;

  /* Paddings */
  --padding-mid: 17px;
  --padding-109xl: 128px;
  --padding-4xs: 9px;

  /* border radiuses */
  --br-xl: 20px;
  --br-8xs: 5px;
  --br-xs: 12px;
  --br-11xl: 30px;
  --br-7xs: 6px;
}
