.freshbooks-logo-1-1-icon1 {
  position: absolute;
  top: calc(50% - 24px);
  left: calc(50% - 82px);
  width: 163.49px;
  height: 48px;
  object-fit: cover;
  display: none;
}
.frame-child1,
.frame-child2,
.frame-child3,
.frame-child4 {
  position: absolute;
  top: 0;
  background-color: var(--product-suite-message);
  opacity: 0.24;
}
.frame-child1 {
  width: 100%;
  right: 0;
  left: 0;
  height: 24px;
}
.frame-child2,
.frame-child3,
.frame-child4 {
  width: 16px;
  height: 80px;
}
.frame-child2 {
  left: 0;
}
.frame-child3,
.frame-child4 {
  right: 0;
}
.frame-child4 {
  width: 100%;
  top: 56px;
  left: 0;
  height: 24px;
}
.guideline1,
.rectangle-group {
  position: absolute;
  top: 0;
  left: 0;
  height: 80px;
}
.rectangle-group {
  width: 100%;
  right: 0;
  overflow: hidden;
  display: none;
  opacity: 0.4;
}
.guideline1 {
  width: 240px;
}
.freshbooks-logo-1-1-icon2 {
  position: absolute;
  top: calc(50% - 32px);
  left: calc(50% - 109px);
  width: 217.99px;
  height: 64px;
  object-fit: cover;
}
.freshbooks {
  position: relative;
  background-color: var(--color-white);
  width: 240px;
  height: 80px;
}
