.icon-pack-child,
.icon-pack-inner,
.icon-pack-item {
  position: absolute;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  height: 0;
  display: none;
}
.icon-pack-inner,
.icon-pack-item {
  top: 50%;
}
.icon-pack-inner {
  top: 100%;
}
.icon-pack {
  position: relative;
  width: 55px;
  height: 51px;
}
@media screen and (max-width: 960px) {
  .icon-pack-child,
  .icon-pack-inner,
  .icon-pack-item {
    display: flex;
  }
}
