@keyframes shadow-drop-bottom {
  0% {
    box-shadow: 0 0 0 0 transparent;
  }
  to {
    box-shadow: 0 12px 20px -12px rgb(0 0 0/35%);
  }
}
.navbar-logo-icon {
  position: relative;
  width: 287px;
  height: 46px;
  object-fit: cover;
}
.college {
  text-decoration: none;
  color: inherit;
}
.college:hover,
.job-seekers:hover {
  cursor: pointer;
}
.college,
.job-seekers {
  position: relative;
  font-weight: 600;
}
.sign-in1 {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 110px;
  height: 51px;
  flex-shrink: 0;
}
.college:hover,
.job-seekers:hover,
.sign-in1:hover {
  background: linear-gradient(#fff, #fff), #13357f;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.sign-in1,
.sign-up {
  text-decoration: none;
  position: relative;
  font-weight: 500;
  color: inherit;
}
.sign-up:hover {
  cursor: pointer;
  animation: 1s ease 0s infinite normal none shadow-drop-bottom;
  opacity: 1;
}
.links,
.sign-up-button {
  display: flex;
  flex-direction: row;
}
.sign-up-button {
  text-decoration: none;
  border-radius: var(--br-7xs);
  background-color: var(--color-darkslateblue);
  width: 119px;
  height: 51px;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-4xs) var(--padding-mid);
  box-sizing: border-box;
  align-items: baseline;
  justify-content: center;
  text-align: center;
  color: var(--color-white);
}
.sign-up-button:hover {
  background-color: var(--color-lightsteelblue);
}
.links {
  flex: 1;
  align-items: center;
  justify-content: space-between;
}
.menu-links {
  width: 1060px;
}
.menu-links,
.navbar-comp,
.navigation {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.navbar-comp {
  flex: 1;
}
.navigation {
  position: absolute;
  top: 21px;
  left: 0;
  width: 1920px;
  height: 85px;
  overflow: hidden;
  padding: var(--padding-mid) var(--padding-109xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-darkslateblue);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 960px) {
  .links {
    display: none;
  }
}
